html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:after, *:before {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.scroll-no {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 6.6rem;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 1rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  display: block;
  width: 100%;
  height: auto;
  max-width: 100%;
}

.header {
  background-color: orangered;
  border-bottom: 3px solid white;
}

@media (max-width: 580px) {
  .contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .contenido-header a {
    display: block;
  }
}
@media (min-width: 581px) {
  .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.contenido-header h1 {
  color: #fff;
}
.contenido-header img {
  display: block;
  width: 6rem;
  height: auto;
}

.nombre {
  display: flex;
  align-items: center;
}

.navegacion-principal a {
  color: #fff;
  font-size: 2.4rem;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 581px) {
  .navegacion-principal a {
    margin-right: 1.5rem;
  }
}
.navegacion-principal a:hover {
  color: #000;
}

@media (min-width: 581px) {
  .barraFija {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
  }
}
.video {
  position: relative;
  max-height: 55rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
}
.video video {
  width: 100%;
}

.contenido-video {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
}
@media (min-width: 475px) {
  .contenido-video {
    width: 50%;
    border-radius: 20px;
  }
  .contenido-video h2 {
    margin-bottom: 0.5rem;
    font-size: 7rem;
    text-transform: uppercase;
    font-weight: bold;
    color: orangered;
  }
  .contenido-video p {
    color: #fff;
    font-size: 3rem;
  }
}
@media (max-width: 574px) {
  .contenido-video {
    width: 70%;
    border-radius: 20px;
  }
  .contenido-video h2 {
    font-size: 3rem;
  }
  .contenido-video p {
    color: #fff;
    font-size: 1.6rem;
  }
}

.sobre-goku {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0rem;
  margin-top: 5rem;
  margin-bottom: 5rem;
  align-items: center;
}
@media (min-width: 932px) {
  .sobre-goku {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.contenido-festival h2 {
  text-transform: uppercase;
  text-align: center;
}
.contenido-festival h2 .goku {
  color: orangered;
  font-weight: bold;
}
.contenido-festival p {
  font-size: 2.3rem;
  text-justify: auto;
  font-weight: 500;
  line-height: 1.3;
}
.contenido-festival p .sangria {
  color: transparent;
}
.contenido-festival p .goku {
  color: orangered;
  font-weight: bold;
}

.bg {
  background-color: black;
}

.galeira {
  text-align: center;
  color: white;
  padding: 2rem;
}
.galeira .goku {
  color: orangered;
  font-weight: bold;
}
.galeira h3 {
  font-size: 2rem;
  text-transform: uppercase;
  max-width: 100%;
  padding: 2rem 0 4rem 0;
}
@media (min-width: 440px) {
  .galeira h3 {
    font-size: 4rem;
  }
}

.imagenes-galeria {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  padding-left: 0;
}
@media (max-width: 600px) {
  .imagenes-galeria {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (max-width: 440px) {
  .imagenes-galeria {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}

.imagenes-galeria .pictures {
  display: block;
  max-width: 100%;
  max-height: 21rem;
  overflow: hidden;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.6);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.cerrarImagen {
  border: none;
  background-color: white;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  padding: 1rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
  transition: background-color 0.3s;
  cursor: pointer;
}
.cerrarImagen:hover {
  background-color: rgb(163, 163, 163);
}

.gokubase {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0rem;
  padding: 3rem 0 3rem 0;
}
@media (min-width: 932px) {
  .gokubase {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.gokubase .gokubase-descripcion {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.gokubase .gokubase-descripcion h3 {
  text-align: center;
}
.gokubase .gokubase-descripcion p {
  font-size: 2.3rem;
