@use 'base/variables' as var;
@use 'base/mixins' as mix;

.bg {
    background-color: black;
}
.galeira {
    text-align: center;
    color: white;
    padding: 2rem;
    .goku {
        color: orangered;
        font-weight: bold;
    }
    h3 {
        @media (min-width: 440px) {
            font-size: 4rem;
        }
        font-size: 2rem;
        text-transform: uppercase;
        max-width: 100%;
        padding: 2rem 0 4rem 0;
    }
}
.imagenes-galeria {
    @media (max-width: 600px) {
        @include mix.grid(2, 1fr, 2rem)
    }
    @media (max-width: 440px) {
        @include mix.grid(1, 1fr, 2rem);
    }
    @include mix.grid(3, 1fr, 2rem);
    padding-left: 0;
}
.imagenes-galeria {
    .pictures {
        display: block;
        max-width: 100%;
        max-height: 21rem;
        overflow: hidden;
    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background-color: rgba(0,0,0,0.6);

    picture {
        display: block;
        max-width: 80rem;
    }
}
.cerrarImagen {
    border: none;
    background-color: white;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
    padding: 1rem;
    position: absolute;
    top: 2rem;
    right: 2rem;
    transition: background-color .3s;
    cursor: pointer;
    &:hover {
        background-color: rgb(163, 163, 163);
    }
}


