@use 'base/variables' as var;
@use 'base/mixins' as mix;

.sobre-goku {
    @media (min-width: 932px) {
        @include mix.grid(2, 1fr, 2rem);
    }
    @include mix.grid(1,1fr,0rem);
    margin-top: 5rem;
    margin-bottom: 5rem;
    align-items: center;
}
.contenido-festival {
    h2 {
        text-transform: uppercase;
        text-align: center;
        .goku {
            color: orangered;
            font-weight: bold;
        }
    }
    p {
        font-size: 2.3rem;
        text-justify: auto;
        font-weight: 500;
        line-height: 1.3;
        .sangria {
            color: transparent;
        }
        .goku {
            color: orangered;
            font-weight: bold;
        }
    }
}