@use 'base/variables' as var;
@use 'base/mixins' as mix;

.gokubase {
    @media (min-width: 932px) {
        @include mix.grid(2, 1fr, 2rem);
    }
    @include mix.grid(1,1fr,0rem);
    padding: 3rem 0 3rem 0;
    .gokubase-descripcion {
        display: flex;
        align-items: center;
        flex-direction: column;
        h3 {
            text-align: center;
        }
        p {
            font-size: 2.3rem;
            text-justify: auto;
            font-weight: 500;
            line-height: 1.3;
        }
        .goku {
            color: orangered;
            font-weight: bold;
        }
        .sangria {
            color: transparent;
        }
    }
}
.gokubase-imagen {
    display: flex;
    align-items: center;
}

