@use 'variables' as var;

@mixin phone {
    @media (max-width: var.$phone) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: var.$desktop) {
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: var.$desktopXL) {
        @content;
    }
}

@mixin grid($cant, $fracc, $espacio) {
    display: grid;
    grid-template-columns: repeat($cant, $fracc);
    gap: $espacio;
}