@use 'base/variables' as var;
@use 'base/mixins' as mix;

.header {
    background-color: orangered;
    border-bottom: 3px solid white;
}
.contenido-header {
    @include mix.phone {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        a {
            display: block;
        }
    }
    @include mix.desktop {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    h1 {
        color: var.$blanco;
    }
    img {
        display: block;
        width: 6rem;
        height: auto;
    }
}
.nombre {
    display: flex;
    align-items: center;
}
.navegacion-principal {
    a {
        color: var.$blanco;
        font-size: 2.4rem;
        font-weight: bold;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 1rem;
        @media (min-width: 581px) {
            margin-right: 1.5rem;
        }


        &:hover {
            color: var.$negro;
        }
    }
}
@include mix.desktop {
    .barraFija {
        left: 0;
        top: 0;
        position: fixed;
        width: 100%;
    }
}
