@use 'base/variables' as var;
@use 'base/mixins' as mix;

.video {
    position: relative;
    max-height: 55rem;
    overflow: hidden;
    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        text-align: center;
    }
    video {
        width: 100%;
    }
}
.contenido-video {
    color: var.$blanco;
    background-color: rgba(0,0,0,0.5);
    @media (min-width: 475px) {
        width: 50%;
        border-radius: 20px;
        h2 {
            margin-bottom: .5rem;
            font-size: 7rem;
            text-transform: uppercase;
            font-weight: bold;
            color: orangered;
        }
        p {
            color: var.$blanco;
            font-size: 3rem;
        }
    }
    @media (max-width: 574px) {
        width: 70%;
        border-radius: 20px;
        h2 {
            font-size: 3rem;
        }
        p {
            color: var.$blanco;
            font-size: 1.6rem;
        }
    }
}