@use 'base/mixins' as mix;

.contenido {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.redes a svg {
    border: 2px solid black;
    border-radius: 50%;
    padding: .3rem;
    margin: .5rem;
    width: 3.7rem;
    height: 3.7rem;
    transition: all .3s;
    &:hover {
        width: 4rem;
        height: 4rem;
    }
}
.copyright {
    p {
        padding: 1rem;
        border-radius: 20px;
        margin: 1rem;
        font-weight: bold;
    }
}
.redes {
    border-radius: 20px;
    a {
        margin-left: 1rem;
    }
}
a:nth-child(3) {
    margin-right: 1rem;
}
.bg1 {
    background-color: orangered;
}