@use 'variables' as var;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
} 
*, *:after, *:before {
    box-sizing: inherit;
}
body {
    font-family: var.$fuente_principal;
    font-size: 1.6rem;

    &.scroll-no {
        overflow: hidden;
    }
    &.body-scroll {
        padding-top: 6.6rem;
    }
}
p {
    font-size: 2rem;
    color: var.$negro;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
h1,h2,h3 {
    margin: 1rem 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
img {
    display: block;
    width: 100%;
    height: auto;
    max-width: 100%;
}